@import '~modularscale-sass/stylesheets/modularscale';
@import './variables';
$ms-base: 1em;
$ms-ratio: 1.333;

$modularscale: (
  base: 1em,
  ratio: 1.333
);



* {
  box-sizing: border-box;
}

html {
  overflow: scroll;
  overflow-x: hidden;
  font-size: ms(0);
}
